.arrow {
  position: absolute;
  top: 50%;
  font-size: 3rem;
  color: #000;
  z-index: 10;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.right {
  right: 32px;
}

.left {
  left: 32px;
}

.slide {
  opacity: 0;
  -webkit-transition-duration: 1.5s ease;
       -o-transition-duration: 1.5s ease;
          transition-duration: 1.5s ease;
}

.slide.active {
  opacity: 1;
  -webkit-transition-duration: 1s;
       -o-transition-duration: 1s;
          transition-duration: 1s;
  -webkit-transform: scale(1.08);
      -ms-transform: scale(1.08);
          transform: scale(1.08);
}


